@import url(https://fonts.googleapis.com/css?family=Nunito);
@import url(https://fonts.googleapis.com/css2?family=Open+Sans&display=swap);
@import url(https://fonts.googleapis.com/css?family=Montserrat);
@import url(https://fonts.googleapis.com/css2?family=Bad+Script&display=swap);
:root{
    --main-color: #0a192f;
    --main-color-light: #112240;
    /* --secondary-color:rgb(255, 235, 59); */
    /* --main-color: #6699FF; */
    --secondary-color: #64ffda;
    /* --main-color: rgb(37, 26, 105); */
    /* --secondary-color: #ffffff; */

    --lightgray: #ccd6f6;
    --graytitle: #a8b2d1;
    --gray: #8892b0;
    --big-title: #fff;
    --brightyellow: rgb(255, 235, 59);
    --hoverbtn:  #64ffdb3f;
    --transition: all 0.25s cubic-bezier(0.645,0.045,0.355,1);
    --logo-font:'Bad Script', cursive;
    --title-font:  'Montserrat', sans-serif;
    --open-sans: 'Open Sans', sans-serif;
}
body {
    background-color: #0a192f;
    background-color: var(--main-color);
    height: auto;
    font-family: 'Montserrat', sans-serif;
    position: relative;
}
/* Main content styles */
.content-section{
    font-family: 'Inter', -apple-system, system-ui, BlinkMacSystemFont, Roboto, "Helvetica Neue", "Segoe UI", Arial, sans-serif;
    background-color: #0a192f;
    background-color: var(--main-color);
    color: #8892b0;
    color: var(--gray);
    padding-left: 3%;
    padding-top: 100px;
}
.section-title{
    margin-bottom: 20px;
}

.hi{
    color: #64ffda;
    color: var(--secondary-color);
    font-family: 'Montserrat', sans-serif;
}

.bigh1 {
    font-size: clamp(40px, 8vw, 80px);
    font-weight: 900;
    letter-spacing: -1px;
    position: relative;
    display: inline-block;
    text-transform: uppercase;
    text-align: center;
    color: #fff;
    color: var(--big-title);
    /* animation: slide-in 1s cubic-bezier(.3, 0, .3, 1) both; */
  animation-delay: calc(60ms * var(--char-index));
  color: saturate(lighten(#182952, 55%), 60%);
  z-index: 0;
}
.bigh1::after {
    content: '';
    position: absolute;
    z-index: -1;
    bottom: 7px;
    left: 0;
    background: #9ea2ac75;
    width: 100%;
    height: 30%;
}
.big-title{
    font-weight: 600;
    margin: 0px;
    font-size: clamp(40px, 8vw, 80px);
}

@keyframes slide-in {
    from {
      transform: translateY(1em);
      opacity: 0;
    }
  }

h2{
    color: #a8b2d1;
    color: var(--graytitle);
    margin: 0px;
    font-size: clamp(40px, 1vw, 80px);
    margin-bottom: 10px;
    font-family: 'Open Sans', sans-serif;
    font-family: var(--open-sans);
    font-weight: bold;
}

.about {
    max-width: 650px;
}
p{
    font-size: 20px;
}
.btn{
    border: 0.5px solid #64ffda;
    border: 0.5px solid var(--secondary-color);
    color: #64ffda;
    color: var(--secondary-color);
    font-family: 'Montserrat', sans-serif;
}
.btn:hover, .btn-primary:focus, .btn-primary:active, .btn-primary.active, .open>.dropdown-toggle.btn-primary {
    background-color: #64ffdb3f;
    background-color: var(--hoverbtn);
    color: #64ffda;
    color: var(--secondary-color);
}
.frame-img{
    max-height: 330px;
    max-width: 338px;
    box-shadow: 0 10px 30px -15px #64ffda;
    box-shadow: 0 10px 30px -15px var(--secondary-color);
    border-radius: 50%;
}
.frame-img img{
  background: #fff;
  border-radius: 50%;
  display: inline-block;
  height: 100%;
  max-height: 330px;
  max-width: 338px;
  position: relative;
  width: 100%;
}
.frame-img::after {
    border: 2px solid #ccd6f6;
    border: 2px solid var(--lightgray);
    top: 20px;
    left: 20px;
    z-index: -1;
}
.panel-section{
    margin-top: 10%;
    padding-top: 150px;
}
.card-p p{
    font-size: 1.2em;
}
.lista-pasatiempos{
    display: flex;
    flex-wrap: wrap;
    position: relative;
    z-index: 2;
    padding: 0px;
    list-style: none;
}
.lista-pasatiempos li{
    margin-right: 1.9em;
    color: #64ffda;
    color: var(--secondary-color);
    font-size: 1.2rem;
}

/* Left sidebar styles */
.left-sidebar{
    color: #0a192f;
    color: var(--main-color);
    font-family: 'Inter', -apple-system, system-ui, BlinkMacSystemFont, Roboto, "Helvetica Neue", "Segoe UI", Arial, sans-serif;
    position: fixed;
    bottom: 0px;
    right: auto;
    z-index: 10;
}
.left-sidebar a{
    color: #ccd6f6;
    color: var(--lightgray);
    font-size: 30px;
    vertical-align: middle;
}
.left-sidebar a:hover{
    color: #64ffda;
    color: var(--secondary-color);
}
.grow{
    transition: all .2s ease-in-out;
}
.grow:hover{
    transform: scale(1.3);
}
.make-me-sticky {
	bottom: 0;
  padding: 0 15px;
}
.make-me-sticky::after {
    content: "";
    display: block;
    width: 2px;
    height: 100px;
    margin: 0px auto;
    background-color: #ccd6f6;
    background-color: var(--lightgray);
}
.make-me-sticky::before {
    content: "";
    display: block;
    width: 2px;
    height: 100px;
    margin: 0px auto;
    margin-bottom: 15px;
    background-color: #ccd6f6;
    background-color: var(--lightgray);
}
/* Barra de iconos en el sidebar */
.icon-bar {
    position: fixed;
    top: 50%;
    transform: translateY(-50%);
  }

  /* Navigation bar styles */
.navbar{
      color: rgb(255, 235, 59);
      color: var(--brightyellow);
      padding: 5px 20px;
      overflow: hidden;
      background: #0a192f;
      background: var(--main-color);
      -webkit-backdrop-filter: blur(10px);
              backdrop-filter: blur(10px);
      box-shadow: 0 10px 30px -10px rgba(2,12,27,0.7);
  }
.nav-link{
    color:#64ffda;
    color:var(--secondary-color);
    font-family: 'Montserrat', sans-serif;
    -webkit-text-decoration-skip: ink;
            text-decoration-skip-ink: auto;
    position: relative;
    font-size: 20px;
}
.nav-link::after{
    content: '';
    position: absolute;
    width: 0; height: 3px;
    display: block;
    margin-top: 1px;
    right: 0;
    background: #64ffda;
    background: var(--secondary-color);
    transition: width .2s ease;
    -webkit-transition: width .2s ease;
}
.nav-link:hover:after{
    width: 100%;
    left: 0;
    background:#64ffda;
    background:var(--secondary-color);
}
.nav-link:hover{
    color:#ccd6f6;
    color:var(--lightgray);
}
.nav-item{
    padding:0 10px;
}
.logo{
    font-family: 'Bad Script', cursive;
    font-family: var(--logo-font);
    font-size: 40px;
    font-weight: 900;
}
/* Estilos de la seccion Habilidades */




/* estilos del footer */
.footer{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    height: auto;
    min-height: 70px;
    padding: 15px;
    text-align: center;
    margin-top: 10%;
    color: #ccd6f6;
    color: var(--lightgray);
}

.footer-icon ul li{
    background-color: #0a192f;
    background-color: var(--main-color);
    border: none;
}
.footer-icon ul li a{
    color: #ccd6f6;
    color: var(--lightgray);
}
.section1{
    min-height: 60vh;
}
/* Media querys para estilos para celulares*/
@media (max-width: 468px) { 
    .col-sidebar{
        position: absolute !important;
        bottom: 0;
        width: 100%;
        display: none;
    }
    .content-section{
        text-align: center;
        margin-left: 0;
        padding-top: 80px;
    }
    .panel-section{
        padding-top: 136px;
        text-align: center;
    }
    .lista-pasatiempos{
        display: inline;
        display: initial;
        flex-wrap: initial;
        position: relative;
        z-index: 2;
        padding: 0px;
        list-style: none;
    }
    .lista-pasatiempos li{
        padding-left: 10%;
    }
    
}
@media (max-width: 768px){
    .img-container{
        display: flex;
        justify-content: center;
        padding-top: 0.8em;
    }
}
/* Media querys para Estilos para tablet y superiores */
@media (min-width: 468px) { 
    .footer-icon{
        display: none;
    }
    .logo{
        font-family: 'Bad Script', cursive;
        font-family: var(--logo-font);
        font-size: 40px;
        font-weight: 900 !important;
    }
}
